.fables {
  color: #fff; }
  .fables .loading {
    padding: 5rem;
    font-family: "DDC Hardware", sans-serif;
    font-size: 3rem;
    color: #666;
    text-align: center;
    -webkit-animation: Pulse 1s infinite;
            animation: Pulse 1s infinite; }

@-webkit-keyframes Pulse {
  0% {
    color: #000; }
  50% {
    color: #FFF; }
  100% {
    color: #000; } }

@keyframes Pulse {
  0% {
    color: #000; }
  50% {
    color: #FFF; }
  100% {
    color: #000; } }



